module.exports = " <div class=seller v-el:seller> <div class=seller-content> <div class=overview> <h1 class=title>{{seller.name}}</h1> <div class=desc> <star :size=36 :score=seller.score></star> <span class=text>({{seller.ratingCount}})</span> <span class=text>月售{{seller.sellCount}}单</span> </div> <ul class=remark> <li class=block> <h2>起送价</h2> <div class=content> <span class=stress>{{seller.minPrice}}</span>元 </div> </li> <li class=block> <h2>商家配送</h2> <div class=content> <span class=stress>{{seller.deliveryPrice}}</span>元 </div> </li> <li class=block> <h2>平均配送时间</h2> <div class=content> <span class=stress>{{seller.deliveryTime}}</span>元 </div> </li> </ul> <div class=favorite @click=toggleFavorite> <span class=icon-favorite :class=\"{'active':favorite}\"></span> <span class=text>{{favoriteText}}</span> </div> </div> <split></split> <div class=bulletin> <h1 class=title>公告与活动</h1> <div class=content-wrapper> <p class=content>{{seller.bulletin}}</p> </div> <ul v-if=seller.supports class=supports> <li class=support-item v-for=\"item in seller.supports\"> <span class=icon :class=classMap[seller.supports[$index].type]></span> <span class=text>{{seller.supports[$index].description}}</span> </li> </ul> </div> <split></split> <div class=pics> <h1 class=title>商家实景</h1> <div class=pic-wrapper v-el:pic-wrapper> <ul class=pic-list v-el:pic-list> <li class=pic-item v-for=\"pic in seller.pics\"> <img :src=pic width=120 height=90 alt=\"\"> </li> </ul> </div> </div> <split></split> <div class=info> <h1 class=title>商家信息</h1> <ul> <li class=info-item v-for=\"info in seller.infos\">{{info}}</li> </ul> </div> </div> </div> ";


